@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
.header {
  display: flex;
  padding: 1rem;
  border-bottom: 1px solid #a3a3a3; }

.header__logo {
  background-image: url(/keep-austin-nerdy/static/media/keep_austin_nerdy.e382fb5f.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  flex-grow: 1;
  height: 4rem;
  margin: 0;
  padding: 0; }

/*---------------- @media -------------------*/
@media screen and (max-width: 475px) {
  .header__logo {
    height: 2.5rem; } }

.page {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.card {
  width: 400px;
  height: 100%;
  max-height: 600px;
  display: flex;
  flex-direction: column; }

.card .card-header__avatar {
  padding: 0.5rem; }

.card .card-content {
  overflow-y: scroll;
  display: flex;
  flex: 1 1; }

.card .card-content,
.card .card-actions,
.card .MuiCardContent-root-71:last-child {
  padding: 1rem; }

.center-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.error {
  justify-content: space-evenly; }

/*---------------- @media -------------------*/
@media screen and (max-width: 475px) {
  .card {
    width: 100%;
    max-height: none; }
  .card .card-header,
  .card .card-content,
  .card .card-actions {
    padding: 0.75rem; } }

.form {
  width: 100%;
  display: flex;
  flex-direction: column; }

.form > div {
  margin-bottom: 0.75rem; }

.form > div:last-child {
  margin-bottom: 0; }

.card-actions__form {
  margin-top: auto; }

.card-actions__form--hide {
  visibility: hidden; }

.answer {
  display: flex;
  flex: 1 1; }

.answer svg {
  color: red;
  margin-right: 0.5rem; }

.answer--correct svg {
  color: green; }

.quiz {
  width: 100%;
  display: flex;
  flex-direction: column; }

.quiz .question,
.quiz > button,
.quiz > div {
  margin-bottom: 1rem; }

.quiz > div:last-child {
  margin-bottom: 0; }

.card-actions .card-actions__finish {
  background-color: green; }

.results {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  flex-direction: column; }

.results > div {
  margin-bottom: 1rem;
  display: flex; }

.results .results__number {
  margin-right: 1.5rem; }

.results .results__number-double {
  margin-right: 0.875rem; }

.results .results__q-and-a {
  width: 100%; }

.results .results__answer {
  background-color: #eeeeee;
  border-radius: 4px;
  text-align: center; }

.not-found {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.not-found .not-found__title {
  margin-bottom: 1rem; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.button-link {
  text-decoration: none; }

.App {
  height: 100vh;
  /* Fallback for browsers that do not support Custom Properties */
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  flex-direction: column; }

